@import "../../../assets/styles/mixin.scss";
@import "../../../assets/styles/variable.scss";

video {
  max-width: 100%;
}

@include mobile {
  .com-scrollVideo {
    font-family: "Montserrat", sans-serif;
    font-size: 2rem;
    font-weight: 800;
    .label {
      color: white;
      text-transform: uppercase;
      padding: 2rem;
      padding-bottom: 0rem;
      background-color: black;
      font-size: 0.85rem;
    }
    .description {
      text-align: justify;
      padding: 2rem;
      padding-top: 0;
      font-size: 1rem;
      font-weight: 400;
      display: grid;
      grid-gap: 1rem;
    }
    .wrapper {
      position: relative;
      background-color: black;
      .title {
        color: white;
        text-transform: uppercase;
        padding: $pdMobile;
        padding-bottom: 0rem !important;
        z-index: 2;
      }

      &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 40vh;
        z-index: -1;
        background-color: black;
      }
    }

    .wrapper-video {
      position: relative;
      height: fit-content;
      margin: $pdMobile;
      margin-top: 1rem !important;
      min-height: 25vh;
      #video-poster {
        width: 100%;
      }
      #btnPlay {
        position: absolute;
        max-width: 15vw;
        left: 50%;
        top: 45%;
        transform: translate(-50%, calc(-50% + 1rem));
        z-index: 10;
        cursor: pointer;
      }
    }
  }
}

@include tablet {
  .com-scrollVideo {
    .wrapper {
      .title {
        font-size: 3rem;
        max-width: 600px;
      }
      &::after {
        height: 35vh;
      }
    }
  }
}

@include desktop {
  .com-scrollVideo {
    .label {
      padding: 2rem $pdDesk;
      margin-bottom: -5px;
      font-size: 1rem;
    }
    .description {
      padding: $pdDesk;
      padding-top: 0;
      grid-gap: 2rem;
      grid-template-columns: repeat(2, 1fr);
    }
    .wrapper {
      .title {
        font-size: 60px;
        padding: unset;
        max-width: unset;
        margin: 0 50vw 2rem $pdDesk;
        @include desktop2k {
          font-size: 5rem;
        }
      }
      &::after {
        min-height: 100vh;
      }
    }
    .wrapper-video {
      margin: 2rem $pdDesk;
      margin-top: 1rem;
      min-height: 70vh;
      @include desktop2k {
        #btnPlay {
          width: 200px;
        }
        #video-poster {
          margin-top: 100px;
        }
      }
    }
  }
}
